<script>
import axios from '@axios'

export default{
  data () {
    return {
      quotaData: null
    }
  },
  methods: {
    printquota () {
      window.print()
    },
    async fetchQuota () {
      const { data } = await axios.get(`/quota/${this.$route.params.id}`)
      this.quotaData = data
    }
  },
  created () {
    this.fetchQuota()
  }
}
